
import Swiper from 'vux/src/components/swiper/swiper.vue'

import { submitProductSNcode } from '@/api/user'
export default {
name: 'guarantee',
components: {
Swiper
},
data () {
return {
swipetoastShow: false,
getCodeStatus: false,
countdown: 60,
postData: {
mob: '',
way: 'pwd',
pwd: ''
},
showtoast: false,
bannerlist: [
{
img: 'static/images/snPIC.jpeg'
}
],
sncode: '',
msg: ''
}
},
methods: {
handleSubmit () {
const reg = /^\d{12}$/
if (this.sncode === '' || !reg.test(this.sncode)) {
this.$vux.toast.text('请填写12位SN码', 'middle')
return
}
const postData = {
sncode: this.sncode
}
console.log(this.sncode)
submitProductSNcode(postData)
.then(res => {
this.$vux.loading.hide()
this.showtoast = true
if (res.data.errorcode === 0) {

this.msg = res.data.errormsg
} else {
this.msg = res.data.errormsg
}
})
.catch(err => {
this.$vux.loading.hide()
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},
scanQRCode () {
const that = this
that.$wechat.scanQRCode({
desc: 'scanQRCode desc',
needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，

success: function (res) {

console.log(res)


that.sncode = res.resultStr.split(',')[1]

that.handleSubmit()
},
error: function (res) {
if (res.errMsg.indexOf('function_not_exist') > 0) {
alert('版本过低请升级')
}
}
})
}
}
}



